import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Header.scss';

class Header extends Component {
  render() {
    var title = "Family Tree Analyzer";
    var subTitle = " - View more information about your family";
    subTitle = "";
    if (this.props.back){
      return(
        <header className="App-header">
            <p className="title">
              <Link to="">{title}<span className="subTitle">{subTitle}</span></Link>
            </p>
            <div className="buttonsWrapper">
              <Link to={this.props.backLocation} className="button">Back</Link>
              <Link to="" className="button">Home</Link>
              <Link to="" className="button">Log Out</Link>
            </div>
          </header>
        )
    } else if (this.props.home){
      return (
      <header className="App-header">
          <p className="title">
          <Link to="">{title}<span className="subTitle">{subTitle}</span></Link>
          </p>
            <div className="buttonsWrapper">
          <Link to="" className="button">Home</Link>
          <Link to="" className="button">Log Out</Link>
          </div>
        </header>
        )
    } else {}
      return (
          <header className="App-header">
            <p className="title">
            <Link to="">{title}<span className="subTitle">{subTitle}</span></Link>
            </p>
            <div className="buttonsWrapper">
            <Link to="" className="button">Log Out</Link>
            </div>
          </header>
      );
    }
  }


export default Header;
