import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from "../header/Header"
import Subheader from "../subHeader/subHeader"
import HomeBox from "../homeBox/HomeBox"
import PersonInfo from "./PersonInfo"
import PersonAverageInfo from "./PersonAverageInfo"
import Bargraph from "../bargraph/Bargraph"
import "./Lifespans.scss"


class LifespansGraph extends Component {
	render() {
        var page = this.props.location.state.page;
        var description;
        var allClass = "selectGroup";
        var motherClass = "selectGroup";
        var fatherClass = "selectGroup";
        if (page === "all"){
            description = "Death age distribution of all of your ancestors";
            allClass += " selected";
        } else if (page === "mother"){
            description = "Death age distribution of your ancestors on your mother's side";
            motherClass += " selected";
        } else {
            description = "Death age distribution of your ancestors on your father's side";
            fatherClass += " selected";
        }
		return (
			<div>
                <Header name=" - Lifespans" backLocation="lifespans" back="true"/>		
                <Subheader familyGroup={page} page="lifespans" back="true" linkTo = "lifespansGraph"/>
                <div className="groupWrapper">
                <Link to={{pathname: "/LifespansGraph", state:{page: "all"}}} className={allClass}>All Ancestors</Link>
                <Link to={{pathname: "/LifespansGraph", state:{page: "mother"}}} className={motherClass}>Mother's Ancestors</Link>
                <Link to={{pathname: "/LifespansGraph", state:{page: "father"}}} className={fatherClass}>Father's Ancestors</Link>

                </div>
                <Bargraph title="Lifespans"
                 description={description}
                 y="Number of people"
                 x = "Age at death"
                 rowCount="9"
                 bargraphClass="lifespans"
                 linkTo="personRows"/>
			</div>
		);
	}
}

export default LifespansGraph;