import React, { Component } from 'react';
import './subHeader.css';
import Tab from "./tab"

class Subheader extends Component {
  render() {
    if (this.props.page == "lifespans"){
      return (
        <div className='subHeader'>
            <Tab name="Lifespans" selected="true" linkTo="lifespans" page="lifespans"/>
            <Tab name="Birthdays" linkTo="birthdays" page="birthdays"/>
            <Tab name="Children"  linkTo="children" page="children"/>
        </div>
    );
    } else if (this.props.page == "birthdays"){
      return (
        <div className='subHeader'>
            <Tab name="Lifespans" linkTo="lifespans" page="lifespans"/>
            <Tab name="Birthdays" selected="true" linkTo="birthdays"  page="birthdays"/>
            <Tab name="Children" linkTo="children" page="children" />
        </div>
    );
    } else {
      return (
        <div className='subHeader'>
            <Tab name="Lifespans" linkTo="lifespans"  page="lifespans"/>
            <Tab name="Birthdays" linkTo="birthdays" page="birthdays" />
            <Tab name="Children" selected="true" linkTo="children" page="children" />
        </div>
    );
    }
  }
}

export default Subheader;
